import * as v from './mutation-types.js'

export default {
	[v.GET_DATA] (state, obj) {
		if(obj.title === state.future.title){
			state.finalData = state.future
		}else if(obj.title === state.jsPrototype.title) {
			state.finalData = state.jsPrototype
		}else if (obj.title === state.es6.title) {
			state.finalData = state.es6
		}else if (obj.title === state.vue.title) {
			state.finalData = state.vue
		}else if (obj.title === state.function.title) {
			state.finalData = state.function
		}else {
			state.finalData = ''
		}
		state.scrollTop = obj.top;
	}
}



