<template lang="html">
	<div class="container">
		<article class="content" v-for="item in Items">
			<div class="blue-strip"></div>
			<span class="type" v-text="item.type"></span>
			<h2 v-text="item.title"></h2>
			<section class="section-content">
				<p v-for="p in item.pItems" v-text="p.pText"></p>
			</section>
			<footer><a @click="goDetail(item.title)">Read More</a></footer>
			<div class="blue-one"></div>
			<div class="blue-two"></div>
		</article>
	</div>
</template>

<script>
export default {
	data () {
		return {
			Items:[
				{ type:'#奇说怪谈', title:'Future', pItems:[{ pText:'You have to trust in something - your gut, destiny, life, karma, whatever，' },
																									{ pText:'Stay Hungry. Stay Foolish.' },
																									{ pText:'...' }]
				},
				{ type:'#函数式编程', title:'函数式编程简介', pItems:[{ pText:'也许会是未来的趋势，'},
																										{ pText:'吸引人的引用透明性和可复用性，' },
																										{ pText:'以及方便测试构建等，' },
																										{ pText:'...' }]
				},
				{ type:'#JavaScript', title:'JavaScript之原型篇', pItems:[{ pText:'人脑非机器，'},
																										{ pText:'时间久后，记忆都会退化，' },
																										{ pText:'因此需要文档辅助作知识沉淀，' },
																										{ pText:'...' }]
				},
				{ type:'#Vue', title:'Vue 2.0双向绑定', pItems:[{ pText:'Vue是一个我比较喜欢的框架，轻快简洁，'},
																										{ pText:'除vue外，我也喜欢React,' },
																										{ pText:'本想写更多点内容，只是vue官网写得蛮好的，而且是中文，就没感觉没必要,' },
																										{ pText:'废话不多说，走起，走起，' },
																										{ pText:'...' }]
				},
				{ type:'#ES6', title:'ECMAScript 6之艺术篇', pItems:[{ pText:'人脑非机器，'},
																										{ pText:'时间久后，记忆都会退化，' },
																										{ pText:'因此需要文档辅助作知识沉淀，' },
																										{ pText:'...' }]
				}
			]
		}
	},
	methods: {
		goDetail(title) {
			this.$router.push(`/${title}`);
			this.$store.dispatch('getData', {
				title: title,
				top: document.body.scrollTop
			});
			document.body.scrollTop = 0;
		}
	}
}
</script>

<style lang="css" scoped>
.content footer {
	padding: 25px 0 25px;
	position: relative;
	z-index: 1000;
}

.content footer a {
	color: rgba(5, 147, 211, 1);
	cursor: pointer;
	padding: 4px 16px;
	border-radius: 5px;
	border: 1px solid rgba(5, 147, 211, 1);
	position: relative;
	background: transparent;
}

.content footer a::after {
	content: "";
	width: 100%;
	height: 100%;
	color: #fff;
	background: rgba(5, 147, 211, 1);
	position: absolute;
	top: 0;
	left:0;
	z-index: -1;
	opacity: 0;
	transform: scale3d(0.7, 1, 1);
	transition: all 0.4s cubic-bezier(0.2, 1, 0.3, 1);
}
.content footer a:hover {
	color: #fff;
}
.content footer a:hover::after {
	opacity: 0.9;
	transform: scale3d(1, 1, 1);
}

</style>



